<template>
    <div class="DetailBox">
        <div class="SDtit">
            问题描述：
        </div>
        <h1 class="detTitle">{{det.k_title}}</h1>
        <div class="infod">
            写作人<i>{{det.k_user_name}}</i>{{det.k_date}}
        </div>
        <div class="DetTontent" v-html="det.k_content"></div>
        <!-- 解决方案 -->
        <div v-if="ISsolution">
            <div class="SDtit">
            解决方案：
            </div>
            <h1 class="detTitle">{{solution.s_title}}</h1>
            <div class="infod">
                写作人<i>{{solution.s_user_name}}</i>{{solution.s_date}}
            </div>
            <div class="DetTontent" v-html="solution.s_content"></div>
        </div>
    </div>
</template>
<script>
import url from "../../common/api";
export default {
    data(){
        return{
            det:{},
            solution:{},
            ISsolution:false
        };
    },
    methods:{
        Fneditknowledge(){//查看单个的知识库详情
            url.viewknowledge({
                k_id:this.$route.query.k_id
            }).then(res=>{
                let xx=res.data.knowledge[0].fields
                xx.k_id=res.data.knowledge[0].pk
                this.det=xx
                console.log(res,'--------')
                // 有解决方案
                if(res.data.solution.length!=0){
                    this.ISsolution=true
                    let sxx=res.data.solution[0].fields
                    sxx.k_id=res.data.solution[0].pk
                    this.solution=sxx
                }
            })
        }
    },
    mounted(){
        this.Fneditknowledge()
    }
}
</script>
<style lang='stylus' scoped>
    .SDtit
      font-size 30px
      margin 30px 0
    .DetailBox
        width 700px
        margin 32px 0 0
        .detTitle
            font-size: 22px;
            line-height: 1.4;
            margin-bottom: 14px;
        .infod
            font-size 14px
            color #999
            i 
                margin 0 10px
                color #576b95
        .DetTontent
            color #595757
            font-size 15px
            letter-spacing 1px
            line-height 1.6
            margin-top 25px
            img 
                max-width 700px
                display block
                margin 0 auto
            .logo
                width 90px
                height 90px
            p 
                text-indent 2em
</style>


